@import '@dpe/base/src/global';

// sass-lint:disable no-vendor-prefixes
@at-root {
  @-ms-viewport { width: device-width; }
}

html {
  box-sizing: border-box;
  -ms-overflow-style: scrollbar;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

// Container
//
// Bootstraps `.container` class is adaptive rather than truely responsive, so we're adding
// `.page-container` as an alternative, for a more fluid and responsive look.
//
// Markup: container.twig
//
// Style guide: 5.container

.page-container {
  @include make-container;
  max-width: $container-width-xl;

  &-narrow {
    @include make-container;
    max-width: $container-width-lg;
  }

  &-fluid {
    @include make-container;
  }

  &-breaker {
    .page-container & {
      width: 100vw;
      position: relative;
      left: 50%;
      right: 50%;
      margin-left: -50vw;
      margin-right: -50vw;
    }
  }

  // Don't pad if nested.
  .page-container,
  .page-container-narrow,
  .page-container-fluid {
    padding-left: 0;
    padding-right: 0;
  }
}

// Grid
//
// See [Bootstrap Grid](https://getbootstrap.com/docs/4.1/layout/grid/) for details on usage.
//
// Markup: grid.twig
//
// Style guide: 5.grid

@import 'bootstrap/scss/grid';

// For cases where a child of a column should also fill the columns height.
.col-fill-height {
  display: flex;
  flex-direction: column;

  > div {
    flex: 1 1 auto;
  }
}

// Page
//
// Custom page level classes that ensure the footer is sticky to the bottom of the browser.
//
// Markup: page.twig
//
// Style guide: 5.page

.page {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  @media print {
    display: block;
    min-height: 0;
  }

  &-middle {
    flex: 1 1 auto;
    align-self: center;
    position: relative;
    width: 100%;
  }
}

